"use client"

import { useState, useEffect } from "react"
import {
  Dialog,
  DialogContent,
} from "@/components/ui/dialog"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Badge } from "@/components/ui/badge"
import { Camera, CheckCircle2, Clock, Zap, Loader2 } from "lucide-react"
import { useToast } from "@/hooks/use-toast"
import { ProductManager } from "@/components/products/ProductManager"
import type { TruckLog } from "@/lib/types"

interface Product {
  id: string
  value: string
  label: string
  isCustom: boolean
}

interface EditLogDialogProps {
  log: TruckLog | null
  open: boolean
  onOpenChange: (open: boolean) => void
  onSuccess: () => void
}

export function EditLogDialog({
  log,
  open,
  onOpenChange,
  onSuccess,
}: EditLogDialogProps) {
  const { toast } = useToast()
  const [isSaving, setIsSaving] = useState(false)
  const [products, setProducts] = useState<Product[]>([])
  const [isLoadingProducts, setIsLoadingProducts] = useState(true)
  const [errors, setErrors] = useState<Record<string, string>>({})
  
  // Form state (matching TruckSection structure)
  const [plate, setPlate] = useState("")
  const [driverName, setDriverName] = useState("")
  const [cargoType, setCargoType] = useState("")
  const [weight, setWeight] = useState("")
  const [comments, setComments] = useState("")
  const [origin, setOrigin] = useState("")
  const [destination, setDestination] = useState("")
  const [senderOrganization, setSenderOrganization] = useState("")
  const [receiverOrganization, setReceiverOrganization] = useState("")
  const [direction, setDirection] = useState<"IN" | "OUT">("IN")

  // Load products on mount
  useEffect(() => {
    async function loadProducts() {
      try {
        setIsLoadingProducts(true)
        const response = await fetch("/api/products")
        if (response.ok) {
          const data = await response.json()
          setProducts(data)
        }
      } catch (error) {
        console.error("Error loading products:", error)
      } finally {
        setIsLoadingProducts(false)
      }
    }

    loadProducts()
  }, [])

  const handleProductAdded = () => {
    // Reload products after adding/deleting
    async function reloadProducts() {
      try {
        const response = await fetch("/api/products")
        if (response.ok) {
          const data = await response.json()
          setProducts(data)
        }
      } catch (error) {
        console.error("Error reloading products:", error)
      }
    }

    reloadProducts()
  }

  // Initialize form data when log changes
  useEffect(() => {
    if (log) {
      setPlate(log.plate || "")
      setDriverName(log.driverName || "")
      setCargoType(log.cargoType || "")
      setWeight(log.weightKg?.toString() || "")
      setComments(log.comments || "")
      setOrigin(log.origin || "")
      setDestination(log.destination || "")
      setSenderOrganization(log.senderOrganization || "")
      setReceiverOrganization(log.receiverOrganization || "")
      setDirection(log.direction)
      setErrors({})
    }
  }, [log])

  const validate = (): boolean => {
    const newErrors: Record<string, string> = {}
    
    if (!plate.trim()) {
      newErrors.plate = "Plate number is required"
    }
    if (!driverName.trim()) {
      newErrors.driverName = "Driver name is required"
    }
    if (!cargoType.trim()) {
      newErrors.cargoType = "Cargo type is required"
    }
    if (!weight.trim()) {
      newErrors.weight = "Weight is required"
    } else if (isNaN(Number(weight)) || Number(weight) <= 0) {
      newErrors.weight = "Weight must be a positive number"
    }

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!log) return

    if (!validate()) {
      return
    }

    setIsSaving(true)
    try {
      const response = await fetch(`/api/logs/${log.id}`, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          direction,
          plate: plate.trim(),
          driverName: driverName.trim(),
          cargoType: cargoType.trim(),
          weightKg: Number(weight),
          comments: comments.trim() || undefined,
          origin: origin.trim() || undefined,
          destination: destination.trim() || undefined,
          senderOrganization: senderOrganization.trim() || undefined,
          receiverOrganization: receiverOrganization.trim() || undefined,
        }),
      })

      const data = await response.json()

      if (!response.ok) {
        throw new Error(data.error || "Failed to update log")
      }

      toast({
        title: "Success",
        description: "Log updated successfully",
      })

      onSuccess()
      onOpenChange(false)
    } catch (error) {
      toast({
        title: "Error",
        description: error instanceof Error ? error.message : "Failed to update log",
        variant: "destructive",
      })
    } finally {
      setIsSaving(false)
    }
  }

  if (!log) return null

  const isSentToCustoms = log.sentToCustoms
  const title = direction === "IN" ? "Truck IN  Gate Entry" : "Truck OUT  Gate Exit"
  const weightLabel = direction === "IN" ? "Gross Weight (kg)" : "Net Weight (kg)"
  
  // Mock plate recognition data
  const confidence = 98.5
  const timestamp = new Date(log.createdAt).toLocaleString("en-US", {
    month: "short",
    day: "numeric",
    year: "numeric",
    hour: "2-digit",
    minute: "2-digit",
  })

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="max-w-4xl max-h-[95vh] overflow-y-auto p-0">
        {isSentToCustoms ? (
          <div className="p-6">
            <p className="text-sm text-gray-600">
              Logs that have been sent to customs cannot be edited for data integrity.
            </p>
          </div>
        ) : (
          <Card className="border-gray-200 bg-white">
            <CardHeader className="pb-4">
              <div className="flex items-center justify-between">
                <CardTitle className="flex items-center gap-2.5 text-gray-900 text-lg font-semibold">
                  <div className="p-2 rounded-lg bg-blue-50">
                    <Camera className="w-5 h-5 text-blue-600" />
                  </div>
                  Edit: {title}
                </CardTitle>
                <Badge className="bg-green-50 text-green-700 border-green-200 px-2.5 py-1">
                  <CheckCircle2 className="w-3.5 h-3.5 mr-1.5" />
                  Recognized
                </Badge>
              </div>
            </CardHeader>
            <CardContent className="space-y-5">
              <form onSubmit={handleSubmit} className="space-y-5">
                {/* License Plate Recognition */}
                <div className="bg-gray-50 rounded-xl p-4 border border-gray-200">
                  <h3 className="text-sm font-semibold text-gray-900 mb-3 flex items-center gap-2">
                    <Camera className="w-4 h-4 text-blue-600" />
                    Real-Time License Plate Recognition
                  </h3>
                  
                  <div className="aspect-video bg-gray-100 rounded-lg overflow-hidden relative border-2 border-gray-200 mb-4">
                    <img 
                      src="/truck-front-view-license-plate.jpg" 
                      alt="Truck camera feed" 
                      className="w-full h-full object-cover" 
                    />
                    <div className="absolute bottom-3 left-3 right-3 bg-white/95 backdrop-blur-sm rounded-lg p-3 border border-blue-200 shadow-sm">
                      <div className="flex items-center justify-between">
                        <div>
                          <p className="text-xs font-medium text-gray-500 mb-1">Detected Plate</p>
                          <p className="text-xl font-mono font-bold text-blue-600 tracking-wider">{plate}</p>
                        </div>
                        <div className="text-right">
                          <p className="text-xs font-medium text-gray-500 mb-1">Confidence</p>
                          <div className="flex items-center gap-1.5">
                            <Zap className="w-4 h-4 text-green-600" />
                            <p className="text-lg font-bold text-green-600">{confidence}%</p>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className="grid grid-cols-2 gap-3">
                    <div className="bg-white rounded-lg p-3 border border-gray-200">
                      <div className="flex items-center gap-2 text-gray-500 mb-1">
                        <Clock className="w-4 h-4" />
                        <span className="text-xs font-medium">Captured</span>
                      </div>
                      <p className="text-sm font-semibold text-gray-900">{timestamp}</p>
                    </div>
                    <div className="bg-white rounded-lg p-3 border border-gray-200">
                      <p className="text-xs font-medium text-gray-500 mb-1">Processing Time</p>
                      <p className="text-sm font-semibold text-gray-900">0.82s</p>
                    </div>
                  </div>
                </div>

                {/* Form Fields */}
                <div className="space-y-4">
                  <div>
                    <Label htmlFor="edit-plate" className="text-sm font-medium text-gray-700">
                      Plate Number
                    </Label>
                    <Input
                      id="edit-plate"
                      value={plate}
                      onChange={(e) => setPlate(e.target.value)}
                      className="mt-1 bg-white border-gray-300 focus:border-blue-500 focus:ring-blue-500"
                      placeholder="Enter plate number"
                    />
                    {errors.plate && (
                      <p className="mt-1 text-xs text-red-600">{errors.plate}</p>
                    )}
                  </div>

                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="edit-direction" className="text-sm font-medium text-gray-700">
                        Direction
                      </Label>
                      <Select
                        value={direction}
                        onValueChange={(value: "IN" | "OUT") => setDirection(value)}
                      >
                        <SelectTrigger className="mt-1 bg-white border-gray-300 focus:border-blue-500 focus:ring-blue-500">
                          <SelectValue />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="IN">IN</SelectItem>
                          <SelectItem value="OUT">OUT</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                    <div>
                      <Label htmlFor="edit-driver" className="text-sm font-medium text-gray-700">
                        Driver Name
                      </Label>
                      <Input
                        id="edit-driver"
                        value={driverName}
                        onChange={(e) => setDriverName(e.target.value)}
                        className="mt-1 bg-white border-gray-300 focus:border-blue-500 focus:ring-blue-500"
                        placeholder="Enter driver name"
                      />
                      {errors.driverName && (
                        <p className="mt-1 text-xs text-red-600">{errors.driverName}</p>
                      )}
                    </div>
                  </div>

                  <div>
                    <div className="flex items-center justify-between mb-2">
                      <Label htmlFor="edit-cargo" className="text-sm font-medium text-gray-700">
                         (Cargo)
                      </Label>
                      <ProductManager products={products} onProductAdded={handleProductAdded} />
                    </div>
                    <Select 
                      value={cargoType} 
                      onValueChange={setCargoType}
                      disabled={isLoadingProducts}
                    >
                      <SelectTrigger className="mt-1 bg-white border-gray-300 focus:border-blue-500 focus:ring-blue-500">
                        <SelectValue placeholder={isLoadingProducts ? "Loading products..." : " "} />
                      </SelectTrigger>
                      <SelectContent>
                        {products.map((product: Product) => (
                          <SelectItem key={product.id} value={product.value}>
                            {product.label}
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                    {errors.cargoType && (
                      <p className="mt-1 text-xs text-red-600">{errors.cargoType}</p>
                    )}
                  </div>

                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="edit-origin" className="text-sm font-medium text-gray-700">
                         (Origin)
                      </Label>
                      <Input
                        id="edit-origin"
                        value={origin}
                        onChange={(e) => setOrigin(e.target.value)}
                        className="mt-1 bg-white border-gray-300 focus:border-blue-500 focus:ring-blue-500"
                        placeholder=" "
                      />
                    </div>
                    <div>
                      <Label htmlFor="edit-destination" className="text-sm font-medium text-gray-700">
                         (Destination)
                      </Label>
                      <Input
                        id="edit-destination"
                        value={destination}
                        onChange={(e) => setDestination(e.target.value)}
                        className="mt-1 bg-white border-gray-300 focus:border-blue-500 focus:ring-blue-500"
                        placeholder=" "
                      />
                    </div>
                  </div>

                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="edit-sender" className="text-sm font-medium text-gray-700">
                          (Sender Organization)
                      </Label>
                      <Input
                        id="edit-sender"
                        value={senderOrganization}
                        onChange={(e) => setSenderOrganization(e.target.value)}
                        className="mt-1 bg-white border-gray-300 focus:border-blue-500 focus:ring-blue-500"
                        placeholder=" "
                      />
                    </div>
                    <div>
                      <Label htmlFor="edit-receiver" className="text-sm font-medium text-gray-700">
                           (Receiver Organization)
                      </Label>
                      <Input
                        id="edit-receiver"
                        value={receiverOrganization}
                        onChange={(e) => setReceiverOrganization(e.target.value)}
                        className="mt-1 bg-white border-gray-300 focus:border-blue-500 focus:ring-blue-500"
                        placeholder="  "
                      />
                    </div>
                  </div>

                  <div>
                    <Label htmlFor="edit-weight" className="text-sm font-medium text-gray-700">
                      {weightLabel}
                    </Label>
                    <Input
                      id="edit-weight"
                      type="number"
                      value={weight}
                      onChange={(e) => setWeight(e.target.value)}
                      className="mt-1 bg-white border-gray-300 focus:border-blue-500 focus:ring-blue-500"
                      placeholder="Enter weight in kg"
                    />
                    {errors.weight && (
                      <p className="mt-1 text-xs text-red-600">{errors.weight}</p>
                    )}
                  </div>

                  <div>
                    <Label htmlFor="edit-comments" className="text-sm font-medium text-gray-700">
                       (Additional Notes)
                    </Label>
                    <Textarea
                      id="edit-comments"
                      value={comments}
                      onChange={(e) => setComments(e.target.value)}
                      className="mt-1 bg-white border-gray-300 focus:border-blue-500 focus:ring-blue-500"
                      placeholder=" ..."
                      rows={3}
                    />
                  </div>
                </div>

                {/* Action Buttons */}
                <div className="flex gap-3 pt-2">
                  <Button
                    type="button"
                    variant="outline"
                    onClick={() => onOpenChange(false)}
                    disabled={isSaving}
                    className="flex-1 border-gray-300 hover:bg-gray-50"
                  >
                    Cancel
                  </Button>
                  <Button
                    type="submit"
                    disabled={isSaving}
                    className="flex-1 bg-blue-600 text-white hover:bg-blue-700"
                  >
                    {isSaving ? (
                      <>
                        <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                        Saving...
                      </>
                    ) : (
                      "Save Changes"
                    )}
                  </Button>
                </div>
              </form>
            </CardContent>
          </Card>
        )}
      </DialogContent>
    </Dialog>
  )
}

